.PageLayout {
	position: relative;

	max-width: 1000px;
	height: auto !important;
	margin: auto;
	padding-left: constant(safe-area-inset-left);
	padding-left: env(safe-area-inset-left);
}

.PageLayout-Nav {
	margin-top: 0.4rem;
}

.PageLayout-Nav_desktop {
	display: none;
}

.PageLayout-Nav_mobile {
	display: block;
	cursor: pointer;
	text-align: center;
	font-size: 16px;
	background: linear-gradient(#fff0e6, #ffd2b6);
	clear: both;
}

@media (min-width: 768px) {
	.PageLayout-Nav_mobile {
		display: none;
	}

	.PageLayout-Nav_desktop {
		display: block;
	}
}

.PageLayout-Sidebar {
	display: none;
	margin-top: 0.4rem;
	padding: 0 0.4rem;
}

.PageLayout-Content {
	margin: 0.4rem 0.5rem 0 0.5rem;
}

@media (min-width: 768px) {
	.PageLayout-Sidebar {
		display: block;
	}

	.PageLayout-Content {
		margin: 0.4rem 0.4rem 0 180px;
	}
}

@media (min-width: 1000px) {
	.PageLayout-Sidebar {
		padding-left: 0;
	}

	.PageLayout-Content {
		margin: 0.4rem 0.4rem 0 175px;
	}
}

.PageLayout-Footer {
	margin: 10px 5px 0 5px;
}

@media (min-width: 768px) {
	.PageLayout-Footer {
		margin-left: 180px;
	}
}

@media (min-width: 1000px) {
	.PageLayout-Footer {
		margin-left: 175px;
	}
}

